.Counter{
    display: flex;
    align-items: center;
}

.Sub,
.Add{
    display: flex;
    justify-content: center;
    align-items: center;
    border:none;
    background-color: #fcbf49;
    width: 36rem;
    height: 36rem;
    border-radius: 50%;
    font-size: 28rem;
    padding: 0;
}
.Sub{
    background-color: #fff;
    border: 1px solid black;
}

.count{
    font-size: 30rem;
    margin:0 5px;
}